const { isDefined } = _uo.utility;
import { convertDelayToSeconds, convertDateToSortableInteger } from '@builder/item/schedule/utilities';
import { itemsObject } from '@builder/common/utilities';

/**
 * Sorts and filters actions by type
 * 
 * @param {Object} options The settings
 * @param {String} options.type The action type: instant, delayed, scheduled
 * @param {String} options.includeOnly An array with actions ID. Only those should be included
 * @returns Array with the actions, sorted
 */
const getActions = ( { type, includeOnly } ) => {
    // Default options
    const options = {
        orderBy: 'menuOrder',
        orderByType: 'int',
        format: 'array',
        filter: {
            itemType: 'action'
        }
    }

    // Check if we should include only some specific actions
    if ( isDefined( includeOnly ) ) {
        options.includeOnly = includeOnly;
    } 

    if ( isDefined( type ) ) {
        // Return the different types of actions
        switch ( type ) {
            case 'instant':
                // Return only instant actions
                options.filter.isScheduled = false;
                options.filter.isDelayed = false;

                break;

            case 'delayed':
                // Return only delayed actions
                options.filter.isScheduled = false;
                options.filter.isDelayed = true;

                // Sort by async_delay_number 
                options.orderBy = ( a, b ) => {
                    return convertDelayToSeconds( a.meta.async_delay_number, a.meta.async_delay_unit ) < convertDelayToSeconds( b.meta.async_delay_number, b.meta.async_delay_unit ) ? -1 : 1;
                }

                break;

            case 'scheduled':
                // Return only scheduled actions
                options.filter.isScheduled = true;
                options.filter.isDelayed = false;

                // Sort by date
                options.orderBy = ( a, b ) => {
                    return convertDateToSortableInteger( a.meta.async_schedule_date, a.meta.async_schedule_time ) < convertDateToSortableInteger( b.meta.async_schedule_date, b.meta.async_schedule_time ) ? -1 : 1;
                }

                break;
        }
    }

    return itemsObject( 'get', options );
}

export default getActions;
