// Import utilities
import { itemsObject } from '@builder/common/utilities';
const { isDefined } = _uo.utility;

/**
 * Adds a property to all actions to easily recognize that they have actions
 *
 * @return {undefined}
 */
const flagActionsScheduled = () => {
	// Get recipe actions
	const actions = itemsObject( 'get', {
		filter: {
			itemType: 'action',
		},
	} );

	// Add the default flag to all actions
	// The default flag is false
	for ( const actionId in actions ) {
		// Get async mode
		const asyncMode = UncannyAutomator.recipe.items[ actionId ].meta[ 'async_mode' ];

		// Check if it has the meta that only scheduled/delayed actions have
		UncannyAutomator.recipe.items[ actionId ].isScheduled = isDefined( asyncMode ) && asyncMode == 'schedule';
		UncannyAutomator.recipe.items[ actionId ].isDelayed = isDefined( asyncMode ) && asyncMode == 'delay';
	}
};

export default flagActionsScheduled;
